        this.apiHeaders = this.apiHeaders.set('Content-Type', 'application/x-www-form-urlencoded');
        this.apiHeaders = this.apiHeaders.set('Access-Control-Allow-Credentials', 'true');

        const scope = 'user_profile,user_media';
